
// 主题颜色
$theme-color: #20ABFE;

// 边框灰色
$border-gray-color: #ebedf0;




















@mixin flex-center($x: center, $y: center) {
  display: flex;
  justify-content: $x;
  align-items: $y;
}

@mixin lineClamp($line: 3) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
}

@mixin ellipsis() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.notification {

  .list {
    margin-bottom: 10px;
    display: flex;
    border-bottom: 1px solid #e6e6e6;

    .left {
      flex: 1;
    }

    .title {
      color: rgba(0, 0, 0, .65);
    }

    .description {
      color: rgba(0, 0, 0, .45);
    }

    .ant-switch {
      align-self: center;
    }
  }
}